.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}





body {
  background: rgba(68, 68, 85, 0);
  color: #FFF;
}

#main {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(100%, rgba(0,0,0,0.66)), to(transparent)), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/336999/shinokubo_test.jpg);
  background: -webkit-linear-gradient(top, rgba(0,0,0,0.66) 100%, transparent), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/336999/shinokubo_test.jpg);
  background: -o-linear-gradient(top, rgba(0,0,0,0.66) 100%, transparent), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/336999/shinokubo_test.jpg);
  background: linear-gradient(to bottom, rgba(0,0,0,0.66) 100%, transparent), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/336999/shinokubo_test.jpg);
  background-size: cover, cover;
  background-position: center, center;
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

h1 {
  font-family:"ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 2rem;
  display: inline-block;
  padding: 1rem;
  font-weight: 400;
  position: relative;
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: opacity 1s ease, -webkit-transform 0.5s ease;
  transition: opacity 1s ease, -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease, opacity 1s ease;
  transition: transform 0.5s ease, opacity 1s ease;
  transition: transform 0.5s ease, opacity 1s ease, -webkit-transform 0.5s ease;
}

.is-loading h1 {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0;
}

h1:before,
h1:after {
  height: 2px;
  width: 100%;
  content: "";
  background: white;
  display: block;
  position: absolute;
  -webkit-transition: width 0.4s ease;
  -o-transition: width 0.4s ease;
  transition: width 0.4s ease;
  -webkit-transition-delay: 0.8s;
       -o-transition-delay: 0.8s;
          transition-delay: 0.8s;
}

h1:before {
  top: 0;
  left: 0;
}

h1:after {
  bottom: 0;
  right: 0;
}

.is-loading h1:before,
.is-loading h1:after {
  width: 0;
}



